<template>
<div ref="wrapper">
  <slot></slot>
</div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
    export default{
      props:{
        probeType:{
          type:Number,//
          default:1
        },
        click:{
          type:Boolean,//
          default:false
        },
        data:{
          type:Array,//
          default:null
        },
        listentScroll:{//
          type:Boolean,
          default :false
        }
      },
        data(){
            return {}
        },
  mounted(){
    setTimeout(()=>{
      this._initScroll();
      //scroll

    },20)
  },
  methods:{
    _initScroll(){
        if(!this.$refs.wrapper){//undefined
          return
        }
        this.scroll=new BScroll(this.$refs.wrapper,{
          scrollY:true,
          scrollX:false,
          probeType:this.probeType,//
          click:this.click,
          //
        })
      if(this.listentScroll){
        const me=this;
        this.scroll.on('scroll',(pos)=>{
          me.$emit('scroll',pos)
          //posposxy

        })
      }
      },
    //20dom

    enable(){
      this.scroll && this.scroll.enable();//
    },
    disable(){
      this.scroll && this.scroll.disable();//,
    },
    refresh(){
      this.scroll && this.scroll.refresh();//scroll
    },
    scrollTo(){
      this.scroll && this.scroll.scrollTo.apply(this.scroll,arguments);
      //scrollTo(),
    },
    scrollToElement(){
      this.scroll && this.scroll.scrollToElement.apply(this.scroll,arguments);
      //scrollTo()
    }

  },
  watch:{
    data(){
      this.refresh()//
    }
  }
    }
</script>


<style scoped>

</style>



