import originJsonp from 'jsonp'
//jsonpjsonpurloptionfn
//urloptionsparam=>fn

export default function Jsonp(url,data,option){
  //url
  //data
  //option
  url +=(url.indexOf('?')<0?'?':'&')+param(data)
  return new Promise((resolve,reject)=>{
    originJsonp(url,option,(error,data)=>{
      if(!error){
        resolve(data)
      }else{
        reject(error)
      }
    })
  })
}

export function param(data){//data
  let url='';
  for(var k in data ){//kdata
    let value=data[k]!==undefined?data[k]:'';//data
    url +=`&${k}=${encodeURIComponent(value)}`//encodeURIComponent
  }
  return url?url.substring(1):""//urlurl&
}



