<template>
<scroll
  class="singerGroup"
  ref="listview"
  :data="data"
  :listentScroll="listentscroll"
  :probeType="probeType"
  @scroll="_scroll"
  >
  <ul>
    <li v-for="singer in data" class="singer_title" ref="listGroup" >
      <h2>{{singer.title}}</h2>
      <ul class="singers">
        <li v-for="item in singer.item" class="singer" @click="selectItem(item)">
          <a href="javascript:" class="singer_head">
          <img v-lazy="item.avatar" :alt="item.name"/>
        </a>
          <div  class="singer_name">
          <a href="javascript:">{{item.name}}</a>
        </div>
        </li>
      </ul>
    </li>
  </ul>
  <div class="list-shortchar" @touchstart="onShortTouchStart" @touchmove.stop.prevent="onShortTouchMove">
    <ul>
      <li v-for="(item,index) in shortcutlist"
          :class="{'active':curIndex===index}"
          :data-index="index">
        {{item}}
      </li>
    </ul>
  </div>
  <div class="list-fixed" ref="fixed">
    <h1 class="fixed-title">{{fixTitle}}</h1>
  </div>
  <div class="list_loading" v-show="!data.length">
    <loading></loading>
  </div>
</scroll>
</template>

<script type="text/ecmascript-6">
  import scroll from 'com/base/scroll'
  import {getData} from 'res/scripts/dom'
  const AUCHOT_HEIGHT=20;//
  const TITLE_HEIGHT=18;
    export default{
      props:{
        data:{
          type:Array,
          default:[]
        }
      },
        data(){
            return {
              scrollY:-1,//y
              curIndex:0,
              diff:-1//scroolYfixedtitle
            }
        },
  computed:{
    shortcutlist(){
      return this.data.map((group)=>{
        return group.title.substr(0,1)//
      })
    },
    fixTitle(){
      if(this.scrollY>0){
        return "";
      }
      return this.data[this.curIndex]?this.data[this.curIndex].title:""
    }
  },
  created(){
    this.touch={};//
    this.listentscroll=true;//
    this.listenHeight=[];//
    this.probeType=3;//scroll013
  },
  components:{
    scroll
  },
  methods:{
    onShortTouchStart(e){
      let authorIndex=getData(e.target,'index');
      //getdatadata-indexdomgetdata
      let firstTouch= e.touches[0]
      this.touch.y1=firstTouch.pageY
      //,created
      // datadomdatacreated
      this.touch.authorIndex=authorIndex;
//      this.curIndex=parseInt(authorIndex);
      //
      this._scrollTo(authorIndex)
      //scrollscrollToElement
    },
    onShortTouchMove(e){
      let firstTouch= e.touches[0]
      this.touch.y2=firstTouch.pageY
      //
      let delta=(this.touch.y2-this.touch.y1)/AUCHOT_HEIGHT |0;
      //,delta;| 0 Math.floor

      let authorIndex=parseInt(this.touch.authorIndex)+delta;
      //
      this._scrollTo(authorIndex)
    },
    _scroll(pos){
      this.scrollY=pos.y;
      //y
    },
    _cacluteHeight(){
      this.listenHeight=[];
      //
      const list=this.$refs.listGroup;
      //
      let height=0;
      //
      this.listenHeight.push(height);
      for(let i=0;i<list.length;i++){
        let item=list[i];
        //item
        height+=item.clientHeight;
        //
        this.listenHeight.push(height)
        //
      }
    },
    _scrollTo(index){
      if(!index && index !==0){
        return;
      }
      if(index<0){
        index=0;
      }else if(index>this.listenHeight.length-2){
        index=this.listenHeight.length-2;
      }
      this.scrollY=-this.listenHeight[index]
      this.$refs.listview.scrollToElement(this.$refs.listGroup[index],0)
    },
    selectItem(item){
      this.$emit('select',item)
    }
  },
  watch:{
    data(){
      let self=this;
      setTimeout(()=>{
        self._cacluteHeight()
        //
      },20)
    },
    scrollY(newY){

      //newYy,
      const listenHeight=this.listenHeight;
      //
      //1:23
      //
      if(newY>0){
        this.curIndex=0;
        return;

      }
      //
      for(let i=0;i<listenHeight.length-1;i++){
        let  height1=listenHeight[i];
        //h1
        let  height2=listenHeight[i+1];
        //h2
        if(-newY>=height1 && -newY < height2){
          this.curIndex=i;
          this.diff=height2+newY;
          return
        }
      }
      //

      this.curIndex=listenHeight.length-2;
    },
    diff(newVal){
      let fixedTop=(newVal>0 && newVal<TITLE_HEIGHT)?newVal-TITLE_HEIGHT:0;
      if(this.fixedTop === fixedTop){
        return
      }
      this.fixedTop=fixedTop;
      this.$refs.fixed.style.transform=`translate3d(0,${fixedTop}px,0)`
    }
  }
    }
</script>


<style scoped lang="scss">
  .singerGroup{
    overflow: hidden;
    position: fixed;
    top: 140px;
    bottom: 0;
    width:100%;
    ul{
      .singer_title{  margin-bottom:20px;
      h2{padding:0 30px;font-size: 36px;color: #cac2c3;background: #eee7f1;font-weight: bold;}
        .singer{
          display:flex;
          .singer_head{
              display: block;
               width: 150px;
              height: 150px;
            margin:20px 15px;
                  img{
                     width: 100%;
                     border-radius: 50%;
               }
          }
          .singer_name{
           height:100%;
            flex: 1;
          text-align: left;padding:50px;
              a{
                font-size: 40px;
                  color: $fontColor;
                 }
              }
          }
      }
    }
  }
  .list-shortchar{
    z-index: 20;
  position: absolute;
  right:0;
  top:50%;
  transform:translateY(-50%);
  width:60px;
  padding:20px 0;
  border-radius:10px;
  text-align:center;
    ul{
      li{
      padding:5px;
      line-height:1;
        font-size: 20px;
        color: #c4bcbd;

      }
  .active{
    color: $colorCur;
  }
    }
  }
  .list-fixed{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    h1{
      padding:0 30px;font-size: 36px;color: #cac2c3;background: #eee7f1;font-weight: bold;
    }
  }
  .list_loading{
    position: absolute;
    width: 100%;
    top:30%;
    left: 50%;
    transform: translateX(-50%)
  }
</style>



